import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/Home.vue"
import Login from "../views/Login.vue"
import Register from "../views/Register.vue"
import UserPlatform from "../views/UserPlatform.vue"
import Create from "../views/Create.vue"
import ViewWorkout from "../views/ViewWorkout.vue"
import ViewOhInfo from "../views/ViewOhInfo.vue"
import ViewUserPost from "../views/ViewUserPost.vue"
import Department from "../views/Department.vue"
import ViewDepartmentrPost from "../views/ViewDepartmentPost.vue"

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/login",
    name: "Login",
    component: Login
  },
  {
    path: "/register",
    name: "Register",
    component: Register
  },
  {
    path: '/create',
    name: 'Create',
    component: Create
  },
  {
    path:"/view",
    name: 'View-Workout',
    component: ViewWorkout
  },
  {
    path:"/platform",
    name: 'User-Platform',
    component: UserPlatform
  },
  {
    path:"/viewInfo",
    name: 'View-Oh-Info',
    component: ViewOhInfo
  },
  {
    path:"/viewUserPost",
    name: 'View-User-Post',
    component: ViewUserPost
  },
  {
    path:"/department",
    name: 'Department',
    component: Department
  },
  {
    path:"/viewDepartmentPost",
    name: 'View-Department-Post',
    component: ViewDepartmentrPost
  }
  
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

// Change document titles


// Route guard for auth routes


export default router;
