<script>
import { ref, computed } from "vue";
import { supabase } from "../supabase/init";
import { useRoute, useRouter } from "vue-router";
import store from "../store/index";
import { uid } from "uid";
export default {
  setup() {
    const data = ref([]);
    const dataLoad = ref(null);
    const errorMsg = ref(null);
    const statusMsg = ref(null);
    const route = useRoute();
    const router = useRouter();
    const user = store.state.user;

    const getData = async () => {
      try {
        const { data: requestInfo, error } = await supabase
          .from("police_request_info")
          .select("*");

        if (error) throw error;
        console.log(requestInfo);
        data.value = requestInfo;

        dataLoad.value = true;
        console.log(data.value);
      } catch (error) {
        errorMsg.value = error.message;
        setTimeout(() => {
          errorMsg.value = false;
        }, 5000);
      }
    };
    getData();

    // Edit mode
    const edit = ref(null);

    const editMode = () => {
      edit.value = !edit.value;
    };

    const update = async (currentId) => {
      console.log(currentId);
      try {
        const { error } = await supabase
          .from("oh")
          .update({
            status: "pending",
          })
          .eq("id", currentId);
        if (error) throw error;
        statusMsg.value = "消息已成功发送至政府部门";
        setTimeout(() => {
          statusMsg.value = false;
        }, 5000);
      } catch (error) {
        errorMsg.value = error.message;
        setTimeout(() => {
          errorMsg.value = false;
        }, 5000);
      }
    };

    return {
      data,
      statusMsg,
      errorMsg,
      dataLoad,
      update,
    };
  },
};
</script>

