<template>
  <div class="max-w-screen-md mx-auto px-4 py-10">
    <div
      v-if="statusMsg || errorMsg"
      class="mb-10 p-4 rounded-md shadow-md bg-light-grey"
    >
      <p class="text-at-light-green">
        {{ statusMsg }}
      </p>
      <p class="text-red-500">
        {{ errorMsg }}
      </p>
    </div>

    <div class="p-8 flex items-start bg-light-grey rounded-md shadow-lg">
      <!--表单-->
      <div v-for="(requestInfo, index) in data" :key="index">
        <div class="flex flex-col gap-y-5 w-full">
          <h1 class="text-2xl text-at-light-green">数据检验</h1>

          <!--表单基本内容-->

          <!--Workout Type-->

          <div class="flex flex-col">
            <label for="workout-name" class="mb-1 text-lg text-at-light-green"
              >公安部信息</label
            >
            <!-- {{ requestInfo }} -->

            <button
              type="submit"
              class="mt-6 py-2 px-6 rounded-sm self-start text-sm text-white bg-at-light-green duration-200 border-solid border-2 border-transparent hover:border-at-light-green hover:bg-white hover:text-at-light-green"
              @click="update('police_request_info', 'sm2')"
            >
              生成加密信息
            </button>
            <label for="workout-name" class="mb-1 text-md text-at-light-green"
              >sm2加密信息</label
            >
            <div
              type="text"
              class="p-2 text-gray-500 focus:outline-none focus:ring-2 focus:ring-green-300"
            >
              {{ sm2EncryptText }}
            </div>
          </div>
          <div class="flex flex-col">
            <!-- <label for="workout-name" class="mb-1 text-lg text-at-light-green"
              >房产局信息</label
            > -->
            <button
              type="submit"
              class="mt-2 py-2 px-6 rounded-sm self-start text-sm text-white bg-at-light-green duration-200 border-solid border-2 border-transparent hover:border-at-light-green hover:bg-white hover:text-at-light-green"
              @click="update('police_request_info', 'sm3')"
            >
              生成散列值
            </button>
            <label for="workout-name" class="mb-1 text-md text-at-light-green"
              >sm3加密信息</label
            >
            <div
              type="text"
              class="p-2 text-gray-500 focus:outline-none focus:ring-2 focus:ring-green-300"
            >
              {{ sm3EncryptText }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, warn, watch } from "vue";
import { supabase } from "../supabase/init";
import { useRoute, useRouter } from "vue-router";
import { sm2, sm3 } from "sm-crypto";
export default {
  setup() {
    const data = ref([]); //存储一站式服务大厅内的数据
    const userInfo = ref([]); //接收各部门传回来的信息
    const valifyInfo = ref({});
    const dataLoad = ref(null);
    const errorMsg = ref(null);
    const statusMsg = ref(null);
    const route = useRoute();
    const router = useRouter();

    const sm2Info = ref("");
    const sm3Info = ref("");

    const check = ref(false); //解密成功判断位
    const equal = ref(false); //判断信息是否一致
    const send = ref(null); //判断请求是否成功
    const requestNumber = ref(""); //业务编号
    const sm2Type = ref(1);
    const sm2EncryptText = ref("");
    const sm2DecryptText = ref("");
    const sm2PrivateKey = ref("");
    const sm2PublicKey = ref("");
    const sm3EncryptText = ref("");
    const sm3Message = ref("");
    const sm3Valid = ref("");

    const departmentInfo = ref({});

    const currentRequestId = route.params.requestNumber; //获取相应的任务编号
    const currentUserId = route.params.idNumber;

    const getUserInfoBackChange = computed(() => {
      return userInfo.value;
    });

    onMounted(() => {
      watch(getUserInfoBackChange, (newVal, oldVal) => {
        console.log(userInfo.value);
      });
    });

    const getData = async () => {
      try {
        const { data: requestInfo, error } = await supabase
          .from("police_department")
          .select("*")
          .eq("id_number", currentUserId);

        if (error) throw error;
        // console.log(requestInfo);
        data.value = requestInfo;
        requestNumber.value = requestInfo[0].requestNumber;
        // d.value.push(requestInfo);

        dataLoad.value = true;
        console.log(data.value);
      } catch (error) {
        errorMsg.value = error.message;
        setTimeout(() => {
          errorMsg.value = false;
        }, 5000);
      }
    };
    getData();

    // Edit mode
    const edit = ref(null);

    const editMode = () => {
      edit.value = !edit.value;
    };

    const update = async (department, type) => {
      if (department === "police_request_info") {
        departmentInfo.value["id_number"] = data.value[0].id_number;
        departmentInfo.value["name"] = data.value[0].name;
        departmentInfo.value["phone_number"] = data.value[0].phone_number;
      } else if (department === "house_management_request_info") {
        departmentInfo.value["house_number"] = data.value[0].house_number;
      }
      await encrypt(JSON.stringify(departmentInfo.value), type, department);
      console.log(sm2EncryptText.value);
      // await decrypt(sm2EncryptText.value, type);
      // console.log(sm2DecryptText.value);
      try {
        if (type === "sm2") {
          const { error } = await supabase
            .from(department)
            .update({
              sm2_info: sm2EncryptText.value,
            })
            .eq("request_number", currentRequestId);
          if (error) throw error;
        } else if (type === "sm3") {
          const { error } = await supabase
            .from(department)
            .update({
              sm3_info: sm3EncryptText.value,
            })
            .eq("request_number", currentRequestId);
          if (error) throw error;
        }

        statusMsg.value = "消息已成功发送至用户端";
        setTimeout(() => {
          statusMsg.value = false;
          // router.push({ name: "View-Oh-Info" });
        }, 3000);
      } catch (error) {
        errorMsg.value = error.message;
        setTimeout(() => {
          errorMsg.value = false;
        }, 3000);
      }
    };

    const sm2Encrypt = async (info, publicKey, cipherMode) => {
      const e = await sm2.doEncrypt(info, publicKey, cipherMode);
      sm2EncryptText.value = e;
      return e;
    };

    const sm2Decrypt = (encrypted, privateKey, cipherMode) => {
      return sm2.doDecrypt(encrypted, privateKey, cipherMode);
    };

    const sm3Encrypt = async (info) => {
      return sm3(info);
    };

    const encrypt = async (d, type, department) => {
      if (type === "sm2") {
        // 从数据库中取出每个用户的密钥
        const { data: keys, error } = await supabase.from("crypto_key").select("*");

        //公钥
        sm2PublicKey.value = keys[0].public_key;

        //加密过程
        sm2EncryptText.value = await sm2Encrypt(d, sm2PublicKey.value, sm2Type.value);
      } else if (type === "sm3") {
        if (!d) {
          errorMsg.value = "请输入数据";
          return;
        }
        sm3EncryptText.value = await sm3Encrypt(d);
      }
    };

    const decrypt = async (d) => {
      // 从数据库中取出每个用户的密钥
      const { data: keys, error } = await supabase.from("crypto_key").select("*");

      console.log(keys);
      console.log(data.value[0].owner);

      //私钥
      sm2PrivateKey.value = keys[0].private_key;
      // 解密过程
      sm2DecryptText.value = sm2Decrypt(d, sm2PrivateKey.value, sm2Type.value);
      // console.log(sm2DecryptText.value);
    };

    return {
      data,
      statusMsg,
      errorMsg,
      dataLoad,
      update,
      sm2Info,
      sm2EncryptText,
      sm2DecryptText,
      sm3Info,
      sm3Valid,
      check,
      sm3EncryptText,
      sm3Message,
      equal,
      userInfo,
      valifyInfo,
      send,
      encrypt,
    };
  },
};
</script>

<style lang="scss" scoped></style>
