  <div class="max-w-screen-md mx-auto px-4 py-10">
    <!--Status Message-->
    <div
      v-if="statusMsg || errorMsg"
      class="mb-10 p-4 bg-light-grey rounded-md shadow-lg"
    >
      <p class="text-at-light-green">{{ statusMsg }}</p>
      <p class="text-red-500">{{ errorMsg }}</p>
    </div>

    <!--创建表单-->
    <div class="p-8 flex items-start bg-light-grey rounded-md shadow-lg">
      <!--表单-->
      <form @submit.prevent="SendPost" class="flex flex-col gap-y-5 w-full">
        <h1 class="text-2xl text-at-light-green">选择政务业务</h1>

        <!--表单基本内容-->
        <div class="flex flex-col">
          <label for="workout-name" class="mb-1 text-lg text-at-light-green"
            >申报名称</label
          >
          <input
            type="text"
            require
            class="p-2 text-gray-500 focus:outline-none focus:ring-2 focus:ring-green-300"
            id="workout-name"
            v-model="requestName"
          />
        </div>

        <!--Workout Type-->
        <div class="flex flex-col">
          <label for="workout-type" class="mb-1 text-lg text-at-light-green"
            >选择办理业务种类</label
          >
          <select
            id="workout-type"
            class="p-2 text-gray-500 focus:outline-none"
            require
            v-model="thingsType"
          >
            <option value="0">选择办理的业务</option>
            <option value="1">不动产业务</option>
            <option value="2">社会保障服务</option>
            <option value="0">...</option>
          </select>
        </div>

        <!--基本信息-->

        <div class="flex flex-col gap-y-4">
          <label for="workout-type" class="mb-1 text-lg text-at-light-green"
            >基本信息</label
          >
          <div class="flex flex-col gap-y-2 relative md:flex-row">
            <div class="flex flex-col mr-3 md:w-1/2">
              <label for="things" class="mb-1 text-sm text-at-light-green"
                >业务编号</label
              >
              <div type="text" class="p-2 w-full text-gray-500 focus:outline-none">
                {{ requestNumber }}
              </div>
            </div>

            <div class="flex flex-col md:w-1/2">
              <label for="part" class="mb-1 text-sm text-at-light-green">审批事项</label>
              <input
                require
                type="text"
                class="p-2 w-full text-gray-500 focus:outline-none focus:ring-2 focus:ring-green-300"
                v-model="things"
              />
            </div>
          </div>

          <div class="flex flex-col gap-y-2 relative md:flex-row">
            <div class="flex flex-col mr-3 md:w-1/2">
              <label for="things" class="mb-1text-sm text-at-light-green">所属部门</label>

              <div class="p-2 w-full text-gray-500 bg-light-grey">{{ department }}</div>
            </div>

            <div class="flex flex-col md:w-1/2">
              <label for="part" class="mb-1 text-sm text-at-light-green">申请人</label>
              <input
                require
                type="text"
                class="p-2 w-full text-gray-500 focus:outline-none focus:ring-2 focus:ring-green-300"
                v-model="owner"
              />
            </div>
          </div>

          <div class="flex flex-col gap-y-2 relative md:flex-row">
            <div class="flex flex-col mr-3 md:w-1/2">
              <label for="things" class="mb-1text-sm text-at-light-green">证件号码</label>
              <input
                require
                type="text"
                class="p-2 w-full text-gray-500 focus:outline-none focus:ring-2 focus:ring-green-300"
                v-model="idNumber"
              />
            </div>

            <div class="flex flex-col md:w-1/2">
              <label for="part" class="mb-1 text-sm text-at-light-green">手机号码</label>
              <input
                require
                type="text"
                class="p-2 w-full text-gray-500 focus:outline-none focus:ring-2 focus:ring-green-300"
                v-model="phoneNumber"
              />
            </div>
          </div>
        </div>
        <!-- 向特定政府部门上传的信息 -->
        <div class="flex flex-col gap-y-4">
          <!-- 不动产业务 -->
          <div v-if="thingsType === '1'">
            <label for="workout-type" class="mb-1 text-lg text-at-light-green"
              >房产局信息</label
            >
            <div class="flex flex-col gap-y-2 relative md:flex-row">
              <div class="flex flex-col mr-3 md:w-1/2">
                <label for="things" class="mb-1text-sm text-at-light-green"
                  >房产证编号</label
                >
                <input
                  require
                  type="text"
                  class="p-2 w-full text-gray-500 focus:outline-none focus:ring-2 focus:ring-green-300"
                  v-model="houseNumber"
                />
              </div>

              <div class="flex flex-col md:w-1/2">
                <label for="part" class="mb-1 text-sm text-at-light-green"
                  >其他信息</label
                >
                <input
                  require
                  type="text"
                  class="p-2 w-full text-gray-500 focus:outline-none focus:ring-2 focus:ring-green-300"
                  v-model="otherInfo"
                />
              </div>
            </div>
          </div>
          <!-- 社会保障业务 -->
          <div v-if="thingsType === '2'">
            <label for="workout-type" class="mb-1 text-lg text-at-light-green"
              >税务局信息</label
            >
            <div class="flex flex-col gap-y-2 relative md:flex-row">
              <div class="flex flex-col mr-3 md:w-1/2">
                <label for="things" class="mb-1text-sm text-at-light-green"
                  >证件号码</label
                >
                <input
                  require
                  type="text"
                  class="p-2 w-full text-gray-500 focus:outline-none focus:ring-2 focus:ring-green-300"
                  v-model="idNumber"
                />
              </div>

              <div class="flex flex-col md:w-1/2">
                <label for="part" class="mb-1 text-sm text-at-light-green"
                  >其他信息</label
                >
                <input
                  require
                  type="text"
                  class="p-2 w-full text-gray-500 focus:outline-none focus:ring-2 focus:ring-green-300"
                  v-model="otherInfo"
                />
              </div>
            </div>
          </div>
        </div>

        <!--申报材料上传以及加密-->
        <div class="flex flex-col gap-y-4">
          <label for="workout-type" class="mb-1 text-lg text-at-light-green"
            >申报材料</label
          >
          <div class="flex flex-row gap-y-2 relative">
            <div class="flex flex-col flex-1">
              <label for="contact" class="mb-1 text-sm text-at-light-green"
                >商品买卖合同</label
              >
              <input
                type="file"
                required
                class="mb-1 text-sm text-center"
                alt="输入图片"
                @change="loadImg"
              />
            </div>
          </div>

          <div class="flex flex-row gap-y-2 relative">
            <div class="flex flex-col flex-1">
              <label for="idInfo" class="mb-1 test-sm text-at-light-green"
                >购房人身份证件</label
              >
              <input
                require
                type="text"
                class="p-2 w-full text-gray-500 focus:outline-none"
                v-model="file"
              />
            </div>
          </div>
        </div>

        <button
          type="submit"
          class="mt-6 py-2 px-6 rounded-sm self-start text-sm text-white bg-at-light-green duration-200 border-solid border-2 border-transparent hover:border-at-light-green hover:bg-white hover:text-at-light-green"
        >
          提交审批
        </button>
      </form>
    </div>
  </div>
</template>

<script>
import { computed, onMounted, ref, watch } from "vue";
import { sm2, sm3 } from "sm-crypto";
import { supabase } from "../supabase/init";
import store from "../store";
import { uid } from "uid";

export default {
  name: "userplatform",
  setup() {
    //Create data
    const requestNumber = ref(uid()); //任务编号
    const workoutName = ref("");
    const workoutType = ref("select-workout");
    const exercises = ref([]);
    const statusMsg = ref(null);
    const errorMsg = ref(null);
    const user = store.state.user;

    //Create cryoto info
    const sm2Type = ref(1);
    // const cipherMode = ref(0);
    const sm2Text = ref("");
    const sm2EncryptText = ref("");
    const sm2PublicKey = ref("");
    const sm3Text = ref("");
    const sm3EncryptText = ref("");

    //file
    const requestInfo = ref({}); //表单信息
    const valifyInfo = ref({}); //用于验证的json文件
    const file = ref("");
    const requestName = ref(""); //申报名称
    const things = ref(""); //审批事项
    const thingsType = ref("");
    const department = ref(""); //申请部门
    const owner = ref(""); //数据拥有者
    const idNumber = ref(""); //身份证号
    const phoneNumber = ref(""); //电话号码
    const avatar_url = ref(""); //文件上传地址
    const img = ref([]); //图片信息
    const img_url = ref(""); //图片地址
    const houseNumber = ref("");
    const otherInfo = ref("");

    const getImgChange = computed(() => {
      return img_url.value;
    });

    //检测业务模板
    const getThingsChange = computed(() => {
      return thingsType.value;
    });

    onMounted(() => {
      watch(
        getImgChange,
        (newVal, oldVal) => {
          // console.log(`图片路径: ${newVal}`);
        },
        { immediate: true, deep: true }
      );
      watch(
        getThingsChange,
        (newVal, oldVal) => {
          if (newVal === "1") {
            department.value = "公安局, 房产局";
          } else if (newVal === "2") {
            department.value = "公安局, 税务局";
          } else {
            department.value = "请选择业务模板";
          }
        },
        { immediate: true, deep: true }
      );
    });

    //更新表单(待改进)
    const updateInfo = async () => {
      requestInfo.value = {
        id: requestNumber.value,
        requestName: requestName.value,
        things: things.value,
        thingsType: thingsType.value,
        department: department.value,
        owner: owner.value,
        idNumber: idNumber.value,
        phoneNumber: phoneNumber.value,
        otherInfo: otherInfo.value,
      };

      valifyInfo.value = {
        idNumber: idNumber.value,
        owner: owner.value,
        phoneNumber: phoneNumber.value,
      };

      if (thingsType.value === "1") {
        requestInfo.value["houseNumber"] = houseNumber.value;
        valifyInfo.value["houseNumber"] = houseNumber.value;
        console.log(requestInfo.value);
      }
      const r = JSON.stringify(requestInfo.value);
      const v = JSON.stringify(valifyInfo.value);
      // console.log(v);
      return { r, v };
    };

    // initRequestInfo();
    //sm2 Function
    /**
     * [SM2Encrypt description]
     * @param {[type]} info       [待加密数据]
     * @param {[type]} publicKey  [公钥 hex]
     * @param {[type]} cipherMode [加密模式 C1C3C2:1, C1C2C3:0]
     * @return {[type]}           [返回加密后的数据 hex]
     */
    const sm2Encrypt = (info, publicKey, cipherMode) => {
      const e = sm2.doEncrypt(info, publicKey, cipherMode);
      sm2EncryptText.value = e;
      return e;
    };

    /**
     * [SM2Decrypt sm2 解密数据]
     * @param {[type]} encrypted  [待解密数据 hex]
     * @param {[type]} privateKey [私钥 hex]
     * @param {[type]} cipherMode [加密模式 C1C3C2:1, C1C2C3:0]
     * @return {[type]}           [返回解密后的数据]
     */
    const sm2Decrypt = (encrypted, privateKey, cipherMode) => {
      return sm2.doDecrypt(encrypted, privateKey, cipherMode);
    };

    const sm3Encrypt = (info) => {
      return sm3(info);
    };

    //加密过程
    const encrypt = async (d, type) => {
      if (type === "sm2") {
        // 从数据库中取出每个用户的密钥
        const { data: keys, error } = await supabase
          .from("crypto_key")
          .select("*")
          .eq("owner", user.email);

        //公钥
        sm2PublicKey.value = keys[0].public_key;

        //加密过程
        sm2EncryptText.value = sm2Encrypt(d, sm2PublicKey.value, sm2Type.value);

        // 解密过程
        //私钥 sm2PrivateKey.value = keys[0].private_key;
        // sm2DecryptText.value = sm2Decrypt(
        //   sm2EncryptText.value,
        //   sm2PrivateKey.value,
        //   sm2Type.value
        // );
        // console.log(sm2DecryptText.value);
      } else if (type === "sm3") {
        if (!d) {
          errorMsg.value = "请输入数据";
          return;
        }
        sm3EncryptText.value = sm3Encrypt(d);
      }
    };

    //上传文件资料
    const uploadImg = async (file, storage) => {
      try {
        const fileName = uid();
        //将附件进行上传
        const { error } = await supabase.storage.from(storage).upload(fileName, file);
        //再经过获取公共地址,将文件地址保留下来
        const publicUrl = await getUrlPublic(fileName, storage);
        if (error) throw error;

        return publicUrl;
      } catch (error) {
        errorMsg.value = `Error: ${error.message}`;
        setTimeout(() => {
          errorMsg.value = false;
        }, 5000);
      }
    };

    const getUrlPublic = async (fileName, storage) => {
      const { publicURL, error } = supabase.storage.from(storage).getPublicUrl(fileName);
      if (error) throw error;
      return publicURL;
    };

    const loadImg = (event) => {
      img.value.push(event.target.files[0]);
    };

    //表单发送
    const SendPost = async () => {
      if (img.value.length > 0) {
        img_url.value = await uploadImg(img.value[0], "avatars");
      }
      const { _, v } = await updateInfo();

      console.log(v);
      await encrypt(v, "sm2");
      await encrypt(v, "sm3");
      try {
        const { error } = await supabase.from("oh").insert([
          {
            sm2Info: sm2EncryptText.value,
            sm3Info: sm3EncryptText.value,
            owner: store.state.user.email,
            requestInfo: requestInfo.value,
            requestNumber: requestNumber.value,
            img_url: img_url.value,
          },
        ]);
        if (error) throw error;
        statusMsg.value = "申请成功";

        (sm2EncryptText.value = ""), (requestInfo.value = []);
        setInterval(() => {
          statusMsg.value = false;
        }, 5000);
      } catch (error) {
        errorMsg.value = `Error: ${error.message}`;
        setTimeout(() => {
          errorMsg.value = false;
        }, 5000);
      }
    };

    return {
      workoutName,
      workoutType,
      exercises,
      statusMsg,
      errorMsg,
      sm2Text,
      sm2EncryptText,
      sm3Text,
      sm3EncryptText,
      encrypt,
      file,
      things,
      thingsType,
      department,
      idNumber,
      requestNumber,
      phoneNumber,
      houseNumber,
      otherInfo,
      user,
      SendPost,
      owner,
      requestName,
      avatar_url,
      loadImg,
      uploadImg,
      img,
    };
  },
};
</script>

<style lang="scss" scoped></style>
