<template>
  <header class="bg-at-light-green text-white">
    <nav class="container py-5 px-4 flex flex-col gap-4 items-center sm:flex-row">
      <div class="flex items-center gap-x-4">
        <img src="../assets/images/dumbbell-light.png" class="w-14" alt="" />
        <h1 class="text-lg">一站式服务大厅</h1>
      </div>
      <ul class="flex flex-1 justify-end gap-x-10">
        <router-link
          v-if="user"
          class="curisor-pointer hover:text-gray-600"
          :to="{ name: 'Home' }"
          >主页</router-link
        >
        <router-link
          v-if="user"
          class="curisor-pointer hover:text-gray-600"
          :to="{ name: 'User-Platform' }"
          >用户端</router-link
        >
        <router-link
          v-if="user"
          class="curisor-pointer hover:text-gray-600"
          :to="{ name: 'View-Oh-Info' }"
          >服务大厅信息管理处</router-link
        >
        <router-link
          v-if="user"
          class="curisor-pointer hover:text-gray-600"
          :to="{ name: 'Department' }"
          >政府部门端</router-link
        >
        <router-link
          v-if="!user"
          class="curisor-pointer hover:text-gray-600"
          :to="{ name: 'Register' }"
          >注册</router-link
        >
        <router-link
          v-if="!user"
          class="curisor-pointer hover:text-gray-600"
          :to="{ name: 'Login' }"
          >登录</router-link
        >
        <li v-if="user" @click="logout" class="cursor-pointer hover:text-gray-600">
          注销
        </li>
      </ul>
    </nav>
  </header>
</template>

<script>
import store from "../store/index";
import { computed } from "vue";
import { supabase } from "../supabase/init";
import { useRouter } from "vue-router";
export default {
  setup() {
    //Get user from store
    const user = computed(() => {
      return store.state.user;
    });

    //Setup ref to router
    const router = useRouter();

    //Logout function
    const logout = async () => {
      await supabase.auth.signOut();
      router.push({ name: "Login" });
    };

    return { logout, user };
  },
};
</script>

