<template>
  <div class="max-w-screen-sm mx-auto px-4 py-10">
    <!-- Error Handling -->
    <div v-if="errorMsg" class="mb-10 p-4 rounded-md bg-light-grey shadow-lg">
      <p class="text-red-500">{{ errorMsg }}</p>
    </div>

    <!-- Register -->
    <form
      @submit.prevent="register"
      class="p-8 flex flex-col bg-light-grey rounded-md shadow-lg"
    >
      <h1 class="text-3xl text-at-light-green mb-4">注册</h1>

      <div class="flex flex-col mb-2">
        <label for="email" class="mb-1 text-sm text-at-light-green">邮箱</label>
        <input
          type="text"
          required
          class="p-2 text-gray-500 focus:outline-none"
          id="email"
          v-model="email"
        />
      </div>

      <div class="flex flex-col mb-2">
        <label for="password" class="mb-1 text-sm text-at-light-green">密码</label>
        <input
          type="password"
          required
          class="p-2 text-gray-500 focus:outline-none"
          id="password"
          v-model="password"
        />
      </div>

      <div class="flex flex-col mb-2">
        <label for="confirmPassword" class="mb-1 text-sm text-at-light-green"
          >确认密码</label
        >
        <input
          type="password"
          required
          class="p-2 text-gray-500 focus:outline-none"
          id="confirmPassword"
          v-model="confirmPassword"
        />
      </div>

      <div class="flex flex-col mb-2">
        <label for="workout-type" class="mb-1 text-sm text-at-light-green"
          >选择身份</label
        >
        <select
          id="workout-type"
          class="p-2 text-gray-500 focus:outline-none"
          require
          v-model="thingsType"
        >
          <option value="0">选择身份</option>
          <option value="1">用户</option>
          <option value="2">管理员</option>
          <option value="3">一站式服务大厅</option>
        </select>
      </div>

      <button
        type="submit"
        class="mt-6 py-2 px-6 rounded-sm self-start text-sm text-white bg-at-light-green duration-200 border-solid border-2 border-transparent hover:border-at-light-green hover:bg-white hover:text-at-light-green"
      >
        注册
      </button>

      <router-link class="text-sm mt-6 text-center" :to="{ name: 'Login' }">
        已经有用户了? <span class="text-at-light-green">登录</span>
      </router-link>
    </form>
  </div>
</template>

<script>
import { ref } from "vue";
import { supabase } from "../supabase/init";
import { useRouter } from "vue-router";
import { sm2 } from "sm-crypto";
export default {
  name: "register",
  setup() {
    // Create data / vars
    const router = useRouter();
    const email = ref(null);
    const password = ref(null);
    const confirmPassword = ref(null);
    const errorMsg = ref(null);
    const sm2PrivateKey = ref("");
    const sm2PublicKey = ref("");

    /**
     * 生成秘钥对
     * @returns { "privateKey": "XXX", "publicKey": "XXXX" }
     */
    const sm2generateKeyPair = () => {
      return sm2.generateKeyPairHex();
    };

    const genereteKeyPair = async () => {
      const data = sm2generateKeyPair();
      sm2PrivateKey.value = data.privateKey;
      sm2PublicKey.value = data.publicKey;
    };

    // Register function
    const register = async () => {
      //用户注册
      if (password.value === confirmPassword.value) {
        try {
          const { error } = await supabase.auth.signUp({
            email: email.value,
            password: password.value,
          });
          //为每个用户分配密钥
          await genereteKeyPair();
          const { error1 } = await supabase.from("crypto_key").insert([
            {
              owner: email.value,
              public_key: sm2PublicKey.value,
              private_key: sm2PrivateKey.value,
            },
          ]);
          if (error || error1) throw error;
          router.push({ name: "Login" });
        } catch (error) {
          errorMsg.value = error.message;
          setTimeout(() => {
            errorMsg.value = null;
          }, 5000);
        }
        return;
      }

      errorMsg.value = "Error: Passwords do not match";
      setTimeout(() => {
        errorMsg.value = null;
      }, 5000);
    };
    return {
      email,
      password,
      confirmPassword,
      errorMsg,
      register,
      sm2PrivateKey,
      sm2PublicKey,
    };
  },
};
</script>
